















































































































































































import { Component, Prop, Vue } from 'vue-property-decorator';


const instID:string="IITKJPMSP"
const bcID:string="admin@iitkgp.ac.in"
const peerID:string="peer0.iitkjp.ac.in"
const apiBase:string="http://apihost:7003/api"

@Component
export default class Home extends Vue {
     
    private instName:string=""
    private instAddr:string=""
    private isBusy:boolean=false

    //Degree details
    private degreeName:string=""
    private degType:string="UG" //DEFAULT VALUE
    private deptName:string="CSE" //DEFAULT VALUE
    private vfrom:string=""
    private vupto:string=""
    private tch:number=0
    private degreeList:any[]=new Array()
    //Student profile updation
    private email:string=""
    private profile:any=null
    mode:string=""
    //Awarding degree
    private studid:string=""
    private degreeUUID:string=""
    private cgpa:string=""

    //View student info
    private studemail:string=""
    private sprofile:any=null
    private studDegreeList:any[]=new Array()
    get isWait(){
        return this.isBusy
    }
    changeMode(mode:string):void{
        this.mode=mode
        if (mode==="vrg" || mode ==="gd"){
            this.getDegreeList()
        }
        
    }
    mounted(){
        this.getProfile()
    }
    async awardDegree(){
        let degreeDetails:any={}
        degreeDetails["degreeuuid"]=this.degreeUUID
        degreeDetails["cgpa"]=this.cgpa
        degreeDetails["studuuid"]=this.studid
        let args=new Array()
        args.push(degreeDetails)
        let request=this.getRequest("registerDegree",args)
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/invoke",request)
 
            if (result.data  && result.data.payload ) {
                console.log(result.data)
                if ( result.data.isSuccess  && result.data.isSuccess === true){
                    alert("Save successful")
                }else{
                    alert(result.data.payload.messsage)
                }
            }else{
                alert("Error in executing the transaction")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async approveStudent(){
        let args=new Array()
        args.push(this.email)
        let request=this.getRequest("approveStudent",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/invoke",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload && result.data.isSuccess) {
                console.log(result.data.payload.payload)
                var record=result.data.payload.payload
                this.profile=result.data.payload.payload
            }else{
                alert("Unable to approve")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    
    async studentSearch(){
        let args=new Array()
        args.push(this.email)
        let request=this.getRequest("queryById",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/query",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload) {
                console.log(result.data.payload.payload)
                var record=result.data.payload.payload
                this.profile=result.data.payload.payload
            }else{
                alert("Record not found")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async studentSearchAllInfo(){
        this.sprofile=null
        let args=new Array()
        args.push(this.studemail)
        let request=this.getRequest("queryById",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/query",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload) {
                console.log(result.data.payload.payload)
                //var record=result.data.payload.payload
                this.sprofile=result.data.payload.payload
            }else{
                alert("Record not found")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
            this.getStudentDegreeList()
        }
        
    }
    async getStudentDegreeList(){
        let args=new Array()
        args.push(this.studemail)
        let request=this.getRequest("searchDegreeByStudentID",args)
        
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/query",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload) {
                console.log(result.data.payload.payload)
                //var record=result.data.payload.payload
                //this.sprofile=result.data.payload.payload
                this.studDegreeList=result.data.payload.payload
            }else{
                //alert("Record not found")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async getDegreeList(){
        
        
        let args=new Array()
        args.push(instID)
        let request=this.getRequest("searchDegreesOffered",args)
        try{
            this.isBusy=true
            let result= await this.axios.post(apiBase+"/query",request)
            console.log(result.data)
            if (result.data && result.data.isSuccess==true && result.data.payload && result.data.payload.payload ){
                //alert("Save successful")
                var records:any=result.data.payload.payload
                if (Array.isArray(records)){
                    this.degreeList=records
                }else{
                    this.degreeList.push(records)
                }
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async addNewDegreee(){
        let degreeDetails:any={}
        degreeDetails["name"]=this.degreeName
        degreeDetails["type"]=this.degType
        degreeDetails["totalCreditHours"]=this.tch
        degreeDetails["offeredByDept"]=this.deptName
        degreeDetails["validFrom"]=this.vfrom
        degreeDetails["validUpto"]=this.vupto
        let args=new Array()
        args.push(degreeDetails)
        let request=this.getRequest("modDegreeInfo",args)
        try{
            this.isBusy=true
            console.log(request)
            let result= await this.axios.post(apiBase+"/invoke",request)
            console.log(result.data)
            if (result.data && result.data.isSuccess==true){
                alert("Save successful")
            }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async updateProfile(){
         let args=new Array()
        args.push(this.instName)
        args.push(this.instAddr)
        let request=this.getRequest("modInstitueRegInfo",args)
        
        try{
            this.isBusy=true
        let result= await this.axios.post(apiBase+"/invoke",request)
        console.log(result.data)
        if (result.data && result.data.isSuccess==true){
            alert("Save successful")
        }
        }catch(err){
            console.log(err)
        }finally{
            this.isBusy=false
        }
    }
    async getProfile(){
        let args=new Array()
        args.push(instID)
        let request=this.getRequest("queryById",args)
        
        try{
            let result= await this.axios.post(apiBase+"/query",request)
            //
            if (result.data  && result.data.payload && result.data.payload.payload) {
                //let record=result.data.payload
                //console.log(record)
                var record=result.data.payload.payload
                this.instAddr=record["addr"]
                this.instName=record["name"]
                //console.log()

            }
        }catch(err){
            console.log(err)
        }
    }
    getRequest(funcName:string,args:any[]):any{
        let request:any= {}
        request["channelID"]="degreerecordchannel"
        request["ccId"]="degreerecordmgmt"
        request["userId"]=bcID
        request["peers"]=[peerID]
        request["funcName"]=funcName
        request["args"]=args
        return request
    }
}
